//colors
$base-color: #ffb800;
$white-color: #f2f2f2;
$black-color: #000;
$red-color: #e82e2e;

//font weight

$font-weight-xs: 100;
$font-weight-sm: 300;
$font-weight-md: 400;
$font-weight-lg: 500;
$font-weight-xl: 700;
$font-weight-xxl: 900;


//font-size

$font-xs: 12px;
$font-sm: 16px;
$font-md: 20px;
$font-lg: 24px;
$font-xl: 28px;
$font-xxl: 32px;

//padding

$p-xs: 4px;
$p-sm: 8px;
$p-md: 12px;
$p-lg: 16px;
$p-xl: 22px;
$p-xxl: 28px;

//margin

$m-xs: 4px;
$m-sm: 8px;
$m-md: 12px;
$m-lg: 16px;
$m-xl: 22px;
$m-xxl: 28px;

//class

.container{
  max-width: 1256px;
  margin: 0 auto
}

.btn-primary{
  background-color: $base-color;
  transition: 0.3s;
  border: 1px solid transparent;
  border-radius: 20px;
  color: #fff;
}

.btn-primary:hover{
  background: $white-color;
  border: 1px solid $base-color;
  color: $base-color;
}

.input-typing{
  border-radius: 20px;
  border: none;
  padding: $p-md 0 $p-md $p-md;
}
.input-typing::placeholder{
  font-size: $font-xs;
}
