@import '../../../main/assets/main';

.left_side{
  width: 30%;
  .banner_link{
    width: 100%;
    position: relative;
    .banner_text_1{
      text-transform: uppercase;
      letter-spacing: 2px;
      text-align: center;
      position: absolute;
      line-height: 20px;
      right: 15px;
      top: 15px;
      font-size: $font-md;
      font-weight: bold;
    }
    .banner_image{
      width: 100%;
      border-radius: 20px;
    }
    &:nth-child(1){
      margin-bottom: $m-lg;
    }
  }
}